// üìÅ frontend/src/embed.jsx
import React from "react";
import ReactDOM from "react-dom/client";
import ChatWidget from "./widget/ChatWidget"; // ‚úÖ nyn√≠ pou≈æ√≠v√°me zabaleno

// üü¢ Glob√°ln√≠ inicializ√°tor widgetu
window.ChatbotWidget = {
  init: (options = {}) => {
    // ‚ùå Zabr√°nƒõn√≠ duplikace mountu
    if (document.getElementById("chatbot-widget-container")) return;

    // üî≤ Vytvo≈ôen√≠ elementu pro mount
    const el = document.createElement("div");
    el.id = "chatbot-widget-container";
    document.body.appendChild(el);

    // üß† Vykreslen√≠ cel√© React komponenty s Tailwind styly uvnit≈ô
    const root = ReactDOM.createRoot(el);
    root.render(
      <ChatWidget
        themeColor={options.themeColor || "#ec4899"}
        introMessage={options.introMessage || "Ahoj! Jak v√°m mohu pomoci s na≈°imi n√°ramky?"}
      />
    );
  },

  destroy: () => {
    const el = document.getElementById("chatbot-widget-container");
    if (el) {
      ReactDOM.createRoot(el).unmount();
      el.remove();
    }
  },
};

// ‚úÖ Automatick√Ω start, pokud nen√≠ vol√°no z HTML
window.addEventListener("DOMContentLoaded", () => {
  if (window.ChatbotWidget && !document.getElementById("chatbot-widget-container")) {
    window.ChatbotWidget.init();
  }
});
